import * as PIXI from "pixi.js";
import Game from "./game";
import { Config } from "./config"
import TWEEN from "@tweenjs/tween.js";
import "pixi-sound"

const height = 600;
const width = 800;

export default class Maension {

  private _lastFrame: number = 0
  private _fpsCount: number = 0
  private _fpsTimer: number = 0
  private _game?: Game

  private readonly _container: HTMLDivElement
  private readonly _renderer: PIXI.Renderer
  private readonly _stage: PIXI.Container
  private readonly _stageWrapper: PIXI.Container
  private readonly _loadingBar: PIXI.Container
  private readonly _fps?: PIXI.Text

  constructor(container: HTMLDivElement) {
    this._container = container;

    this._renderer = PIXI.autoDetectRenderer({
      width,
      height,
      backgroundColor: Config.constants.black
    });

    this._container.appendChild(this._renderer.view);

    console.log(
      "Viewport",
      this._container.clientWidth,
      this._container.clientHeight
    );

    // create the root of the scene graph
    this._stage = new PIXI.Container();

    this._stageWrapper = new PIXI.Container();
    this._stageWrapper.addChild(this._stage)

    if (Config.showFPS) {
      this._fps = new PIXI.Text("FPS: ", {
        fontWeight: "16px",
        fill: "#fff"
      } as PIXI.TextStyle);
      this._fps.x = 10
      this._fps.y = 10
      this._stageWrapper.addChild(this._fps)
    }

    this._loadingBar = new PIXI.Container()
    const outline = new PIXI.Graphics();
    outline.lineStyle(
      5,
      Config.constants.white
    );

    const lHeight = 50
    const lWidth = 300
    outline.drawRect(
      width / 2 - lWidth / 2, height / 2 - lHeight / 2,
      lWidth,
      lHeight
    );

    this._loadingBar.addChild(outline)
    this._stageWrapper.addChild(this._loadingBar)

    this.resize()
    window.addEventListener("resize", this.resize.bind(this));

    requestAnimationFrame(this._tick.bind(this));
  }

  resize() {
    const vpw = this._container.clientWidth - 50;  // Width of the viewport
    const vph = this._container.clientHeight - 50; // Height of the viewport
    let nvw; // New game width
    let nvh; // New game height

    // The aspect ratio is the ratio of the screen's sizes in different dimensions.
    // The height-to-width aspect ratio of the game is HEIGHT / WIDTH.

    if (vph / vpw < height / width) {
      // If height-to-width ratio of the viewport is less than the height-to-width ratio
      // of the game, then the height will be equal to the height of the viewport, and
      // the width will be scaled.
      nvh = vph;
      nvw = (nvh * width) / height;
    } else {
      // In the else case, the opposite is happening.
      nvw = vpw;
      nvh = (nvw * height) / width;
    }

    if (nvw > width) {
      nvw = width
    }

    if (nvh > height) {
      nvh = height
    }

    // Set the game screen size to the new values.
    // This command only makes the screen bigger --- it does not scale the contents of the game.
    // There will be a lot of extra room --- or missing room --- if we don't scale the stage.
    this._renderer.resize(nvw, nvh);

    // This command scales the stage to fit the new size of the game.
    this._stageWrapper.scale.set(nvw / width, nvh / height);
  }

  /**
   *
   */
  public run() {
    const options = {
      crossOrigin: true
    } as PIXI.ILoaderOptions

    const assetPath = "assets";

    const loader = new PIXI.Loader()

    // load map
    loader.add("maps.house", assetPath + "/maps/house.json", options)
    loader.add("maps.street", assetPath + "/maps/street.json", options)
    loader.add("maps.maension", assetPath + "/maps/maension.json", options)

    // load player
    loader.add("ryu.stance", assetPath + "/ryu/stance.json", options)
    loader.add("ryu.punch", assetPath + "/ryu/punch.json", options)
    loader.add("ryu.kick", assetPath + "/ryu/kick.json", options)
    loader.add("ryu.walk", assetPath + "/ryu/walk.json", options)
    loader.add("ryu.gothit", assetPath + "/ryu/gothit.json", options)
    loader.add("ryu.died", assetPath + "/ryu/died.json", options)

    // enemies
    loader.add("bat.walk", assetPath + "/enemys/bat/walk.json", options)

    // weapons
    loader.add("weapons.shotgun", assetPath + "/weapons/shotgun.json", options)
    loader.add("weapons.beretta", assetPath + "/weapons/beretta.json", options)

    // sounds
    loader.add("bgm.title", assetPath + "/sounds/title.m4a", options)
    loader.add("beretta.fire", assetPath + "/sounds/beretta/mansion_sfx_gun_berreta_outside.wav", options)
    loader.add("beretta.reload", assetPath + "/sounds/beretta/mansion_sfx_gun_berreta_reload.wav", options)

    // load things, like decals heads and so on
    loader.add("things.blood", assetPath + "/things/blood.json", options)
    loader.add("things.door", assetPath + "/things/door.json", options)
    loader.add("things.hud", assetPath + "/things/hud.json", options)
    loader.add("things.decals", assetPath + "/things/decals.json", options)
    loader.add("things.muzzle", assetPath + "/things/muzzle.json", options)

    loader.onComplete.once(this._assetsLoaded.bind(this))

    // resources loaded
    loader.load();

    loader.onProgress.add((event) => {
      this.updateLoadingBar(event.progress)
    })
  }

  private updateLoadingBar(pct: number) {
    const fill = new PIXI.Graphics();
    fill.lineStyle(
      5,
      Config.constants.white
    );

    fill.beginFill(Config.constants.white)

    const lHeight = 50
    const lWidth = 300
    fill.drawRect(
      width / 2 - lWidth / 2, height / 2 - lHeight / 2,
      lWidth / 100 * pct,
      lHeight
    );

    this._loadingBar.addChild(fill)
  }

  /**
   *
   * @param loader
   * @param resources
   * @private
   */
  private _assetsLoaded(
    loader: PIXI.Loader,
    resources: PIXI.IResourceDictionary
  ) {
    this._stageWrapper.removeChild(this._loadingBar)

    // loading done, start game
    this._game = new Game(
      resources,
      this._stage,
      this._renderer.width,
      this._renderer.height
    );

    console.log("Mänsion:", "Init done, ready to scare!");
  }

  /**
   *
   * @private
   */
  private _tick(elapsed: number) {
    // calculate frame delta
    let delta = elapsed - this._lastFrame;
    this._lastFrame = elapsed;

    if (this._fps) {
      this._fpsCount++
      this._fpsTimer += delta;

      // reset after 1 second
      if (this._fpsTimer > 1000) {
        // update text
        this._fps.text = "FPS: " + this._fpsCount;

        this._fpsCount = 0;
        this._fpsTimer = 0;
      }
    }

    // update game
    if (this._game) {
      this._game.tick(elapsed, delta);
    }

    // render the container
    this._renderer.render(this._stageWrapper);

    requestAnimationFrame(this._tick.bind(this));

    TWEEN.update(elapsed)
  }
}
