import * as PIXI from "pixi.js"
import { AnimationBase } from "./animation-base"
import { Config } from "../config"

PIXI.settings.SCALE_MODE = PIXI.SCALE_MODES.NEAREST;

/**
 *
 */
export class TextWithBackground extends AnimationBase {

  constructor(
    resources: PIXI.IResourceDictionary,
    text: string,
    fontSize: string = "8px",
    fontColor: string = "#fff",
    background: number = Config.constants.black
  ) {
    super(resources);

    const t = new PIXI.Text(
      text, {
        fontSize,
        fill: fontColor,
        fontFamily: "Press Start 2P"
      } as PIXI.TextStyle)
    const bg = new PIXI.Sprite(
      PIXI.Texture.WHITE
    );

    bg.width = t.width
    bg.height = t.height
    bg.tint = background

    this.addChild(bg)
    this.addChild(t)
  }
}