import * as PIXI from "pixi.js"
import { MapBase } from "./map-base"
import { Config } from "../config"
import { Keyboard } from "../tools"
import { TextWithBackground } from "../objects/text-with-background"

/**
 *
 */
export class Title extends MapBase {

  enterText: PIXI.Container
  bgm

  constructor(
    resources: PIXI.IResourceDictionary,
    width: number,
    height: number
  ) {
    super(resources, width, "maps.maension");

    this.bgm = resources["bgm.title"].sound
    this.bgm.volume = 0.3
    this.bgm.play()

    this.withHud = false;
    this.withCrosshair = false;
    this.withPlayer = false;
    this.withMouse = false;

    const brand = new TextWithBackground(resources, "Mänsion", "54px", "#EFB775")
    brand.x = width / 2 - brand.width / 2
    brand.y = height / 3 - brand.height / 2
    this.addChild(brand)

    this.enterText = new PIXI.Container();
    this.enterText.addChild(new TextWithBackground(resources, "Press Enter to play", "24px"))

    this.enterText.x = width / 2 - this.enterText.width / 2
    this.enterText.y = height / 2 - this.enterText.height / 2

    const backdrop = new PIXI.Sprite(
      PIXI.Texture.WHITE
    );

    backdrop.width = width
    backdrop.height = height
    backdrop.tint = Config.constants.black

    let maension = new PIXI.Sprite(
      this._textures!["Maension_WIP.png"]
    );

    maension.alpha = 0.04
    const scale = 2
    maension.scale.x = scale;
    maension.scale.y = scale;

    maension.y = height - maension.height
    maension.x = width / 2 - maension.width / 2

    this._foregroundContainer.addChild(backdrop)
    this._foregroundContainer.addChild(maension)
    this._foregroundContainer.addChild(this.enterText)

    const enter = new Keyboard("Enter", true);

    enter.pressEvent = () => this.done()
  }

  protected done() {
    this.bgm.stop();
    super.done();
  }

  public tick(delta: number) {
    this.enterText.alpha += delta / 2000
    if (this.enterText.alpha >= 1) {
      this.enterText.alpha = 0
    }
  }
}