import React, { useEffect, useRef, useState } from 'react';
import './MaensionComponent.css';
import Maension from "../game/maension"

export default function MaensionComponent() {

  const stage = useRef(null)
  const [maension, setMaension] = useState<Maension>()

  useEffect(() => {
    if (!maension && stage.current) {
      const m = new Maension(stage.current!)
      m.run()
      setMaension(m)
    }
  }, [stage, maension])

  return (
    <div className="maension" ref={stage}/>
  );
}
